<template>
  <div class="ps-3 pe-3">
    <!--Add buttons to initiate auth sequence and sign out-->
    <div
      class="mb-4 d-flex flex-row event"
      v-for="task in tasks"
      :key="task.id"
    >
      <span class="line rounded me-3 mb-2 mt-2" />
      <div class="d-flex flex-column flex-grow-1">
        <span class="text-start">{{ task.summary }}</span>
        <span class="text-end text-secondary">{{ task.start }}</span>
      </div>
    </div>
    <button id="authorize_button" @click="handleAuthClick">Authorize</button>
    <button id="signout_button" @click="handleSignoutClick">Sign Out</button>
  </div>
</template>

<script>
import { loadScript } from '../../node_modules/vue-plugin-load-script'
// TODO(developer): Set to client ID and API key from the Developer Console

// Discovery doc URL for APIs used by the quickstart
const DISCOVERY_DOC =
  'https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest'

// Authorization scopes required by the API; multiple scopes can be
// included, separated by spaces.
const SCOPES = 'https://www.googleapis.com/auth/calendar.readonly'

export default {
  components: {},
  name: 'TasksComponent',
  data () {
    return {
      info: null,
      tokenClient: null,
      gapiInited: false,
      gisInited: false,
      tasks: [],
      allTasks: false,
      google: {
        client_id: '',
        api_key: ''
      }
    }
  },
  methods: {
    /**
     * Callback after api.js is loaded.
     */
    gapiLoaded: function () {
      window.gapi.load('client', this.intializeGapiClient)
    },

    /**
     * Callback after the API client is loaded. Loads the
     * discovery doc to initialize the API.
     */
    intializeGapiClient: async function () {
      const appData = this
      await window.gapi.client.init({
        apiKey: appData.google.API_KEY,
        discoveryDocs: [DISCOVERY_DOC]
      })
      this.gapiInited = true
      this.maybeEnableButtons()
    },

    /**
     * Callback after Google Identity Services are loaded.
     */
    gisLoaded: function () {
      const appData = this
      this.tokenClient = window.google.accounts.oauth2.initTokenClient({
        client_id: appData.google.CLIENT_ID,
        scope: SCOPES,
        callback: '' // defined later
      })
      this.gisInited = true
      this.maybeEnableButtons()
    },

    /**
     * Enables user interaction after all libraries are loaded.
     */
    maybeEnableButtons: function () {
      if (this.gapiInited && this.gisInited) {
        document.getElementById('authorize_button').style.visibility = 'visible'
      }
    },

    /**
     *  Sign in the user upon button click.
     */
    handleAuthClick: function () {
      this.tokenClient.callback = async (resp) => {
        if (resp.error !== undefined) {
          throw resp
        }
        document.getElementById('signout_button').style.visibility = 'visible'
        document.getElementById('authorize_button').innerText = 'Refresh'
        await this.listUpcomingEvents()
      }

      if (window.gapi.client.getToken() === null) {
        // Prompt the user to select a Google Account and ask for consent to share their data
        // when establishing a new session.
        this.tokenClient.requestAccessToken({ prompt: 'consent' })
      } else {
        // Skip display of account chooser and consent dialog for an existing session.
        this.tokenClient.requestAccessToken({ prompt: '' })
      }
    },

    /**
     *  Sign out the user upon button click.
     */
    handleSignoutClick: function () {
      const token = window.gapi.client.getToken()
      if (token !== null) {
        window.google.accounts.oauth2.revoke(token.access_token)
        window.gapi.client.setToken('')
        document.getElementById('content').innerText = ''
        document.getElementById('authorize_button').innerText = 'Authorize'
        document.getElementById('signout_button').style.visibility = 'hidden'
      }
    },

    /**
     * Print the summary and start datetime/date of the next ten events in
     * the authorized user's calendar. If no events are found an
     * appropriate message is printed.
     */
    listUpcomingEvents: async function () {
      let response
      try {
        const request = {
          calendarId: 'primary',
          timeMin: new Date().toISOString(),
          showDeleted: false,
          singleEvents: true,
          maxResults: 10,
          orderBy: 'startTime'
        }
        response = await window.gapi.client.calendar.events.list(request)
      } catch (err) {
        document.getElementById('content').innerText = err.message
        return
      }

      const events = response.result.items
      if (!events || events.length === 0) {
        document.getElementById('content').innerText = 'No events found.'
        return
      }
      const output = JSON.parse(JSON.stringify(events))
      console.log(output)
      // Flatten to string to display
      output.forEach((event) => {
        this.tasks.push({
          id: event.id,
          summary: event.summary,
          start: event.start.dateTime || event.start.date
        })
      })
      localStorage.lastEvents = JSON.stringify(this.tasks)
    }
  },
  mounted () {
    document.getElementById('authorize_button').style.visibility = 'hidden'
    document.getElementById('signout_button').style.visibility = 'hidden'

    const data = JSON.parse(localStorage.settings)
    this.google.client_id = data.google.client_id
    this.google.api_key = data.google.api_key

    loadScript('https://apis.google.com/js/api.js')
      .then(() => this.gapiLoaded())
      .catch(() => {
        // Failed to fetch script
      })

    loadScript('https://accounts.google.com/gsi/client')
      .then(() => this.gisLoaded())
      .catch(() => {
        // Failed to fetch script
      })

    if (localStorage.lastEvents) {
      this.tasks = JSON.parse(localStorage.lastEvents)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.event .line{
  background-color: var(--bs-primary);
  min-width: 5px;
}
.text-secondary {
  font-size: 80%;
}
</style>
