<template>
  <div v-if="info" ref="info" >
    <div
      @click="showMore"
      class="d-flex flex-column animated temp vw-100"
      ref="window"
      :class="{ 'expand': showmore, 'colapse': !showmore }"
    >
      <div ref="panel" class="pt-2 pb-2 bg-transparent">
        <div class="d-flex justify-content-around">
          <div>
            <span class="mt-4 mb-4"
              ><img width="100px" :src="image(icon)"
            /></span>
            <span class="display-1 mb-4">{{ parseInt(current) }} &#176;</span>
          </div>
        </div>
        <div class="ps-3 d-flex d-flex justify-content-between">
          <shortLayout title="MaxTemp" :value="max" code="°" />
          <shortLayout title="MinTemp" :value="min" code="°" />
        </div>
        <div class="ps-3 d-flex justify-content-between">
          <shortLayout title="Humidity" :value="humidity" code="%" />
          <shortLayout title="RealFeel" :value="feels_like" code="°" />
        </div>
      </div>
      <div
        class="d-flex flex-column flex-nowrap vw-100 flex-grow-1 pt-1 ps-3 pe-3"
        style="margin: 0px; scroll-snap-type: x mandatory"
      >
        <div
          v-for="item in info.data.list"
          :key="item.dt"
          class="
            d-flex
            flex-row
            align-items-center
            mb-3
            detail
          "
          style="min-width: 40vw; scroll-snap-align: center"
        >
          <span class="p-3"> {{ moment(item.dt_txt).format("HH") }} Uhr</span>
          <span><img width="100px" :src="image(item.weather[0].icon)" /></span>
          <span class=""> {{ item.weather[0].description }}</span>
          <span class="p-3" style="min-width: 80px" > {{ parseInt(item.main.temp) }} &#176;</span>
        </div>
      </div>
    </div>
    <div class="position-absolute bottom-0 end-0 overflow-hidden" style="height: 80px; width: 120px;">
      <button
        class="btn btn-primary rounded-circle m-1 animated "
        :class="{ fadeInRight: showmore, fadeOutRight: !showmore }"
      >
        <RefreshCwIcon @click="reload" />
      </button>
    </div>
  </div>
</template>

<script>
import shortLayout from './shortLayout.vue'
import axios from '../../node_modules/axios'
import moment from '../../node_modules/moment'
import { RefreshCwIcon } from '../../node_modules/vue-feather-icons'

export default {
  components: { shortLayout, RefreshCwIcon },
  name: 'CurrentWeather',
  data () {
    return {
      info: null,
      max: 0,
      min: 0,
      current: 0,
      icon: '',
      humidity: 0,
      feels_like: 0,
      showmore: false,
      initStyle: true
    }
  },
  methods: {
    showMore: function () {
      this.showmore = !this.showmore
    },
    moment: function (time) {
      return moment(time)
    },
    image: function (code) {
      return 'http://openweathermap.org/img/wn/' + code + '@2x.png'
    },
    maxTemp: function (list) {
      list.forEach((element) => {
        if (this.max < element.main.temp_max) {
          this.max = parseInt(element.main.temp_max)
        }
        if (this.min === 0) {
          this.min = element.main.temp_min
        }
        if (this.humidity === 0) {
          this.humidity = parseInt(element.main.humidity)
        }
        if (this.min > element.main.temp_min) {
          this.min = parseInt(element.main.temp_min)
        }
        if (this.current === 0) {
          this.current = element.main.temp
        }
        if (this.feels_like === 0) {
          this.feels_like = parseInt(element.main.feels_like)
        }
        if (this.icon === '') {
          this.icon = element.weather[0].icon
        }
      })
    },
    weather: function () {
      axios
        .get(
          'https://api.openweathermap.org/data/2.5/forecast/?q=Berlin,de&appid=8021b4e46c033f8148e926c465998bee&units=metric&lang=de&cnt=10'
        )
        .then((response) => {
          this.info = response
          this.maxTemp(response.data.list)
          setTimeout(() => {
            if (this.initStyle) {
              this.initStyle = false
              const maxHeight = this.$refs.panel.clientHeight + 'px'
              // this.$refs.info.style.height = maxHeight
              this.$refs.window.style.setProperty('--max-height', maxHeight)
              this.$refs.window.style.setProperty(
                '--offset-top',
                0 - this.$refs.info.offsetTop + 'px'
              )
              this.$refs.window.style.setProperty('--offset-bottom', 'calc(' + maxHeight + ' - 100vh)')
              console.log(this.$refs.panel.getBoundingClientRect())
            }
            this.$refs.window.addEventListener('animationend', (event) => {
              if (event.animationName.indexOf('colapse') >= 0) {
                this.$refs.info.style.zIndex = this.showmore ? '1' : 'unset'
              }
            })
            this.$refs.window.addEventListener('animationstart', (event) => {
              if (event.animationName.indexOf('expand') >= 0) {
                this.$refs.info.style.zIndex = this.showmore ? '1' : 'unset'
              }
            })
          }, 100)
        })
    },
    reload: function () {
      this.weather()
    }
  },
  mounted () {
    this.weather()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.animated.temp {
  --max-height: 293px;
  --offset-top: -56px;
  --offset-bottom: calc(293px - 100vh);
  z-index: 1 !important;
  height: var(--max-height);
  max-height: var(--max-height);
  background-color: var(--bs-gray-100);
  overflow: hidden;
}

.btn {
  margin: -10px;
  padding: 10px;
  height: 50px;
  width: 50px;
}

.expand {
  animation-name: expand;
}

.colapse {
  animation-name: colapse;
}

@keyframes expand {
  0% {
    margin-top: 0px;
    margin-bottom: var(--offset-bottom);
    max-height: var(--max-height);
    min-height: var(--max-height);
    background-color: var(--bs-gray-100);
  }
  99%{
    overflow: hidden;
  }
  100% {
    margin-top: var(--offset-top);
    margin-bottom: 0px;
    min-height: 100vh;
    max-height: 100vh;
    background-color: var(--bs-white);
    overflow-y: auto;
  }
}
@keyframes colapse {
  0% {
    margin-top: var(--offset-top);
    margin-bottom: var(--offset-bottom);
    min-height: 100vh;
    max-height: 100vh;
    background-color: var(--bs-white);
  }
  99% {
    z-index: unset;
  }
  100% {
    max-height: var(--max-height);
    min-height: var(--max-height);
    background-color: var(--bs-gray-100);
    margin-top: 0px;
    margin-bottom: 0px;
  }
}
@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(-10px, 0, 0);
  }
}

.fadeInRight {
  animation-delay: 0.5s;
  animation-name: bounceInRight;
}
@keyframes fadeOutRight {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes bounceInRight {
  from,
  60%,
  75%,
  90%,
  to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  from {
    opacity: 0;
    transform: translate3d(3000px, 0, 0) scaleX(3);
  }

  60% {
    opacity: 1;
    transform: translate3d(-25px, 0, 0) scaleX(1);
  }

  75% {
    transform: translate3d(10px, 0, 0) scaleX(0.98);
  }

  90% {
    transform: translate3d(-5px, 0, 0) scaleX(0.995);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes bounceOutRight {
  20% {
    opacity: 1;
    transform: translate3d(-20px, 0, 0) scaleX(0.9);
  }

  to {
    opacity: 0;
    transform: translate3d(2000px, 0, 0) scaleX(2);
  }
}

.fadeOutRight {
  animation-delay: 0.5s;
  animation-name: bounceOutRight;
}
</style>
